.container {
    display: grid;
    grid-template-columns: auto auto auto ;
  row-gap: 200px;
   
  }

  h1 {
      color: #03989e;
      padding-bottom: 25px;
  }

  .card {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #000;
    box-shadow: 0 5px 10px rgba(0,0,0, 0.5);
    width: 300px;
    height: 450px;
  }
  
  .card .poster {
    position: relative;
    overflow: hidden;
  }
  
  .card .poster:before {
    content: "";
    position: absolute ;
    bottom: -190px;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg,#000 50%, transparent);
    transition: 0.5s;
    z-index: 1;
  }
  
  .card:hover .poster:before {
    bottom: 0;
  }
  
  .card .poster img {
    width: 100%;
    transition: 0.5s;
  }
  
  .card:hover .poster img {
    filter: blur(2px);
    transform: translateY(-30px);
  }
  
  .details {
    position: absolute;
    padding: 20px;
    width: 100%;
    height: 74%;
    bottom: -190px;
    left: 0;
    box-sizing: border-box;
    transition: 0.5s;
    z-index: 2;
  }
  
  .card:hover .details {
    bottom: 0;
  }
  
  .details h2 {
    color: #fff;
    margin: 0;
    padding: 0;
    font-size: 20px;
  }
  
  .details h2 span {
    font-size: 14px;
    color: #03989e;
  }
  
  .rating {
    position: relative;
    padding: 5px, 0;
  }
  
  .rating .fa {
    color: #f7f406;
    margin-right: 2px;
    font-size: 16px;
  }
  
  .rating span {
    color: #fff;
    padding: 0 5px;
  }
  
  .tags {
    position: relative;
    margin-top: 5px;
  }
  
  .tags span {
    padding: 2px 5px;
    margin-right: 2px;
    color: #fff;
    display: inline-block;
    border-radius: 4px;
  }
  
  .tags span.fantasy {
    background: #03989e;
    text-transform: uppercase;

  }
  
  
  .info {
    color: #fff;
  }
  
  .info p {
    margin: 15px, 0, 10px;
  }
  
  .star {
    position: relative;
  }
  
  .star h4 {
    margin: 0;
    padding: 0;
    font-size: 20px;
    color: #03989e;
    text-decoration: underline;
  }
  
  .star ul {
    margin: 10px 0 0;
    padding: 0;
    display: flex;
  }
  
  .star ul li {
    list-style: none;
    width: 45px;
    height: 45px;
    background: #fff;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 6px;
    border: 2px solid #fff;
  }
  
  .star ul li img {
    width: 100%;
  }

  a {
text-decoration: none;  }


@media only screen and (max-width: 700px) {
  .container {
    display: grid;
    grid-template-columns: 100% ;
  row-gap: 200px;
   
  }
}

@media screen and (max-width: 1200px) and (min-width: 700px) {
  .container {
    display: grid;
    grid-template-columns: 50% 50% ;
  row-gap: 200px;
   
  }
}

